import router from "@/router"
import axios from "axios"
export default {
  data() {
    return {
      name: "",
      email: "",
      contact: "",
      password: "",
      confirmPassword: "",
      error: "",
      sucess: "",
      creating: false,
      baseUrl: "https://production-api-rentable.onrender.com/users/register",
    }
  },

  mounted() {
  document.addEventListener('keyup', this.verifyEnter);
  },
  methods: {

     async verifyEnter() {
         if (event.key === 'Enter' || event.keyCode === 13) {
        // Coloque aqui a função que você deseja executar
        this.register();
      }
    },
    async register() {
      this.error = ""

      if (this.password != this.confirmPassword) {
        return (this.error = "Are you sure that informations are right?")
      }

      await axios

        .post(`${this.baseUrl}`, {
          name: this.name,
          email: this.email,
          contact: this.contact, 
          password: this.password,
        })
        .then(res => {
          this.creating = true
          console.log(res)

          this.sucess = `Sucess, ${this.name} ❤`

          setTimeout(() => {
            this.$router.push("/login")
          }, 3000)
        })
        .catch(err => {
          if (!Array.isArray(err.response.data.message)) {
            this.error = err.response.data.message;
          } else {
            this.error = err.response.data.message[0];
          }
      
        })
    },
  },
}
